<script>
import bus from "@/bus.js";
import lessonTypes from "./../lessonTypes";
import {
  Table,
  TableColumn,
  DropdownMenu,
  DropdownItem,
  Dropdown,
  Tooltip,
} from "element-ui";

export default {
  name: "inline-actions-table",
  components: {
    [Tooltip.name]: Tooltip,
    [Table.name]: Table,
    [TableColumn.name]: TableColumn,
    [Dropdown.name]: Dropdown,
    [DropdownItem.name]: DropdownItem,
    [DropdownMenu.name]: DropdownMenu,
  },
  data() {
    return {
      lessonTypes,
      currentPage: 1,
    };
  },
  mounted() {
    let self = this
    bus.$on("onDelete", data => {
      self.onDelete(data)
    })
  },
  methods: {
    onEdit(row) {
    //   alert(`You want to edit ${row.name}`);
      alert(`You want to edit`);
    },
    onDelete(row) {
      let self = this
      self.lessonTypes.some((item, index) => {
        if (item.id === row.id) {
          self.lessonTypes.splice(index, 1)
        }
      })
    },
    preview(data) {
      this.$emit("preview", data)
    }
  },
};
</script>
